import React, { useCallback, useState } from 'react'
import {
  Avatar,
  Card,
  ListItem,
  Icon,
  Overlay,
  Button,
} from 'react-native-elements'
import { Text, Linking, View, Modal } from 'react-native'
import { H3 } from '@expo/html-elements'
import {
  DEFI_EXPLORER_ETHEREUM,
  DEFI_DAPPRADAR,
  DEFI_DEBANK,
  DEFI_ZAPPER,
  DEFI_ZERION,
} from 'staker-freenodes'

import commonStyle from 'helpers/commonStyle'
import useLocalStorage from 'hooks/useLocalStorage'

export const DefiExplorer = ({ term, chainId }) => {
  const [visible, setVisible] = useState(false)
  const [explorer, setExplorer] = useLocalStorage(`dapp_explorer`, DEFI_DEBANK)

  const toggleOverlay = useCallback(() => setVisible(!visible), [visible])

  const SelectDappExplorerModal = () => (
    <Modal
      animationType='slide'
      transparent
      visible={visible}
      onRequestClose={toggleOverlay}
    >
      <Card>
        <Card.Title>Pick the DAPP Explorer </Card.Title>
        <View style={[commonStyle.row, commonStyle.alignSelfCenter]}>
          {Object.keys(DEFI_EXPLORER_ETHEREUM).map((entry) => {
            const { name } = DEFI_EXPLORER_ETHEREUM[entry]
            // console.log(entry, name)
            return (
              <Button
                key={entry}
                title={`üè† ${name}`}
                type={entry === explorer ? 'solid' : 'outline'}
                onPress={() => {
                  toggleOverlay()
                  setExplorer(entry)
                }}
                buttonStyle={{ marginRight: 4 }}
              />
            )
          })}
        </View>
        <Button
          title='Close'
          icon={{
            name: 'cross',
            type: 'entypo',
            color: '#2089dc',
          }}
          iconRight
          onPress={toggleOverlay}
          type='outline'
          style={[commonStyle.alignSelfCenter, { marginTop: 8 }]}
        />
      </Card>
    </Modal>
  )

  const explorerUrl = (addr) => {
    const dapp = DEFI_EXPLORER_ETHEREUM[explorer]
    return dapp.url(addr)
  }

  return (
    <View style={[commonStyle.row, commonStyle.alignSelfCenter]}>
      <Icon
        name='box'
        type={commonStyle.selectIconType}
        size={24}
        onPress={() => Linking.openURL(explorerUrl(term))}
        iconStyle={commonStyle.buttonMarginRight}
      />
      <Icon
        name={commonStyle.selectIconName}
        type={commonStyle.selectIconType}
        color={commonStyle.selectIconColor}
        size={24}
        onPress={toggleOverlay}
      />
      <Overlay ModalComponent={SelectDappExplorerModal} />
    </View>
  )
}

export default DefiExplorer
