import React from 'react'
import { View } from 'react-native'
import { Avatar, Card, ListItem, Text } from 'react-native-elements'
import { useEthscanBalance, useEthscanTokensBalance } from 'staker-hooks'

import { CHAIN_MAP } from 'helpers/constants'
import commonStyle from 'helpers/commonStyle'
import { DefiStakes } from 'components/DeFi'
import { Explorers } from 'components/Explorers'

export const EthScanBalance = ({ chainId, term }) => {
  const addresses = [term]
  const [nativeLoading, nativeBalance] = useEthscanBalance(addresses, chainId)
  const [tokenLoading, tokenBalance] = useEthscanTokensBalance(
    addresses,
    chainId
  )
  // console.count('balance')
  // console.log('>>>', chainId, nativeLoading, tokenLoading)

  if (nativeLoading || tokenLoading) {
    return (
      <Card>
        <Card.Title>üí∞ {CHAIN_MAP[chainId]} Balance</Card.Title>
        <Card.Divider />
        <ListItem>
          <Text>Loading</Text>
        </ListItem>
      </Card>
    )
  }

  const balance = [...nativeBalance, ...tokenBalance]

  return (
    <Card wrapperStyle={commonStyle.row}>
      <View>
        <Card.Title>üí∞ {CHAIN_MAP[chainId]} Balance</Card.Title>
        <Explorers chainId={chainId} term={term} />
        <Card.Divider />
        {balance.length > 0 &&
          balance.map((token) => (
            <ListItem key={token.token}>
              <ListItem.Content>
                <ListItem.Title>
                  {token.token.padEnd(20, ' ‚Äã')} {token.balance}
                </ListItem.Title>
                {/* <ListItem.Subtitle></ListItem.Subtitle> */}
              </ListItem.Content>
            </ListItem>
          ))}
      </View>
      <DefiStakes chainId={chainId} term={term} />
    </Card>
  )
}

export default EthScanBalance
